import React,{Component} from "react";
import Entrees from "../components/Entrees";
import axios from "axios";
import {setEntrees} from "../actions/entreeActions";
import {connect} from "react-redux";
class EntreesContainer extends Component{
    state = {
        isLoading:true,
        errors:[]
    }
    async componentDidMount() {
        await axios.get('http://localhost:49652/api/entrees')
        .then(res=>{
            const json =  res.data;
            this.props.setEntrees(json);    
        })
        .then(()=>{
            this.setState({
                isLoading:false
            });
            console.log('EntreesContainer componentDidMount');
            console.log(this.props);
        })
        .catch(function (error) {
            // handle error
            this.setState({
                errors:error
            });
        });
        
    } 
    render(){
        return(
            <Entrees entrees={this.props.entrees} isLoading={this.state.isLoading}/>
        )
    }
}
const mapStateToProps=state=>({
    entrees:state.entrees.entrees
})
export default connect(mapStateToProps,{setEntrees})(EntreesContainer)