import { SHOW_STRONGS_DISHES } from "../constants/strongDishTypes";
const initialState={
    strongsDishes:[]
}
export default function(state=initialState,action){
    switch (action.type) {
        case SHOW_STRONGS_DISHES:
            return{
                ...state,
                strongsDishes:action.payload
            }
        default:
            return state;
    }
}