import React , {Component} from "react";
import Desserts from "../components/Desserts";
import {setDesserts} from "../actions/dessertActions";
import {connect} from "react-redux";
import axios from "axios";
class DessertsContainer extends Component{
    state = {
        isLoading:true,
        errors:[]
    }
    async componentDidMount() {
        await axios.get('http://localhost:49652/api/desserts')
        .then(res => {
            const desserts = res.data;
            this.props.setDesserts(desserts); 
        }).then(()=>{
            this.setState({
                isLoading:false
            });
            console.log('DessertsContainer componentDidMount');
            console.log(this.props);
        })
        .catch(function (error) {
            // handle error
            this.setState({
                errors:error
            });
        });
    }
    render(){
        return(
            <Desserts desserts={this.props.desserts} isLoading={this.state.isLoading}
            addToCart={this.props.addToCart}/>
        )
    }
}
const mapStateToProps=state=>({
    desserts:state.desserts.desserts
})
export default connect(mapStateToProps,{setDesserts})(DessertsContainer);