import React,{Component} from "react";
import PropTypes from 'prop-types';
class Entrees extends Component{
    render(){
        const {isLoading,entrees}=this.props;
        return(
            <React.Fragment>
            {!isLoading ? ( 
                 entrees.map(entree => 
                        <div className="menu-body menu-left menu-white"> 
                            <div className="menu-thumbnail">
                                <img className="img-responsive center-block" src={entree.picture} alt={entree.name}/>
                            </div>  
                            <div className="menu-details"> 
                                <div className="menu-title clearfix">
                                <h4>{entree.name}</h4>
                                    <span className="price">$ {entree.price}</span>
                                </div>
                                <div className="menu-description">
                                    <p>{entree.description.substr(0,80)+'...'}</p>
                                </div>
                                <button type="button" className="btn btn-danger">Add To Cart</button>
                            </div>
                        </div>
            )
            ):(
                <div>Loading Drinks</div>
            )}
            </React.Fragment>
        );
    }
}
Entrees.propTypes = {
    isLoading:PropTypes.boolean,
    entrees: PropTypes.shape({
        picture:PropTypes.string,
        name:PropTypes.string,
        price:PropTypes.number,
        description:PropTypes.string
    })
}
export default Entrees;