import React , {Component} from "react";
import PropTypes from 'prop-types';
class Desserts extends Component{
    constructor(props){
        super(props);
        this.addDessert=this.addDessert.bind(this);
    }
    addDessert(idDessert){
        this.props.addToCart(idDessert);
    }
    render(){
        const {isLoading,desserts}=this.props;
        return(
            <React.Fragment>
            {!isLoading ? ( 
                 desserts.map(dessert => 
                        <div className="menu-body menu-left menu-white"> 
                            <div className="menu-thumbnail">
                                <img className="img-responsive center-block" src={dessert.picture} alt={dessert.name}/>
                            </div>  
                            <div className="menu-details"> 
                                <div className="menu-title clearfix">
                                <h4>{dessert.name}</h4>
                                    <span className="price">$ {dessert.price}</span>
                                </div>
                                <div className="menu-description">
                                    <p>{dessert.description.substr(0,80)+'...'}</p>
                                </div>
                                <button type="button" className="btn btn-danger" onClick={()=>{this.addDessert(dessert.idDessert)}}>Add To Cart</button>
                            </div>
                        </div>
            )
            ):(
                <div>Loading Desserts</div>
            )}
            </React.Fragment>
        );
    }
}
Desserts.propTypes = {
    isLoading:PropTypes.boolean,
    desserts: PropTypes.shape({
        picture:PropTypes.string,
        name:PropTypes.string,
        price:PropTypes.number,
        description:PropTypes.string
    }),
    addDessert:PropTypes.func.isRequired
}
export default Desserts;