import React , {Component} from "react";
import PropTypes from 'prop-types';
class Drinks extends Component{
    render(){
        const {isLoading,drinks}=this.props;
        return(
            <React.Fragment>
            {!isLoading ? ( 
                 drinks.map(drink => 
                        <div className="menu-body menu-left menu-white"> 
                            <div className="menu-thumbnail">
                                <img className="img-responsive center-block" src={drink.picture} alt={drink.name}/>
                            </div>  
                            <div className="menu-details"> 
                                <div className="menu-title clearfix">
                                <h4>{drink.name}</h4>
                                    <span className="price">$ {drink.price}</span>
                                </div>
                                <div className="menu-description">
                                    <p>{drink.description.substr(0,80)+'...'}</p>
                                </div>
                                <button type="button" className="btn btn-danger">Add To Cart</button>
                            </div>
                        </div>
            )
            ):(
                <div>Loading Drinks</div>
            )}
            </React.Fragment>
        );
    }
}
Drinks.propTypes = {
    isLoading:PropTypes.boolean,
    drinks: PropTypes.shape({
        picture:PropTypes.string,
        name:PropTypes.string,
        price:PropTypes.number,
        description:PropTypes.string
    })
}
export default Drinks;