import React , {Component} from "react";
import Drinks from "../components/Drinks";
import axios from "axios";
import {setDrinks} from "../actions/drinkActions";
import {connect} from "react-redux";
class DrinksContainer extends Component{
    state = {
        isLoading:true,
        errors:[]
    }
    async componentDidMount() {
        await axios.get('http://localhost:49652/api/drinks')
        .then(res => {
            const drinks = res.data;
            this.props.setDrinks(drinks)
        }).then(()=>{
            this.setState({
                isLoading:false
            })
            console.log('DrinksContainer componentDidMount');
            console.log(this.props);
        })
        .catch(function (error) {
            // handle error
            this.setState({
                errors:error
            });
        })
    }
    render(){
        return(
            <Drinks drinks={this.props.drinks} isLoading={this.state.isLoading}/>
        )
    }
}
const mapStateToProps=state=>({
    drinks:state.drinks.drinks
})
export default connect(mapStateToProps,{setDrinks})(DrinksContainer);
