import React,{Component} from "react";
import StrongsDishes from "../components/StrongsDishes";
import axios from "axios";
import {setStrongsDishes} from "../actions/strongDishActions";
import {connect} from "react-redux";
class StrongsDishesContainer extends Component{
    state = {
        isLoading:true,
        errors:[]
    }
    async componentDidMount() {
        await axios.get('http://localhost:49652/api/strongs-dishes')
        .then(res=>{
            const json =  res.data;
            this.props.setStrongsDishes(json)
        })
        .then(()=>{
            this.setState({
                isLoading:false
            });
            console.log('StrongsDishesContainer componentDidMount');
            console.log(this.props);
        })
        .catch(function (error) {
            this.setState({
                errors:error
            });
        })
    }
    render(){
        return(
            <StrongsDishes strongsDishes={this.props.strongsDishes} isLoading={this.state.isLoading}/>
        )
    }
}
const mapStateToProps=state=>({
    strongsDishes:state.strongsDishes.strongsDishes
})
export default connect(mapStateToProps,{setStrongsDishes})(StrongsDishesContainer)